import { Injectable } from '@angular/core';

import { NxMessageService } from './nx-message.service';
import { NxCacheService } from './nx-cache.service';
import { NxCookieService } from './nx-cookie.service';
import { NxLocalService } from './nx-local.service';
import { NxUtilsService } from './nx-utils.service';

@Injectable()
export class NxService {

  constructor(
    public message : NxMessageService,
    public cache : NxCacheService,
    public cookie : NxCookieService,
    public utils : NxUtilsService,
    public local : NxLocalService
  ) { }

}



