import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { MaterialModule } from "./material/material.module";

import { AppComponent } from './app.component';
import { SharedModule } from "./shared/shared.module";
import { AppRoutingModule, AppRoutedComponents } from "./app.routing";
import { SidebarComponent } from './components/admin/common/sidebar/sidebar.component';
import { FooterComponent } from './components/admin/common/footer/footer.component';
import { HeaderComponent } from './components/admin/common/header/header.component';
import { FixedpluginComponent } from './components/admin/common/fixedplugin/fixedplugin.component';


@NgModule({
  declarations: [
    AppComponent,
    AppRoutedComponents,
    SidebarComponent,
    FooterComponent,
    HeaderComponent,
    FixedpluginComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    HttpModule,
    MaterialModule,
    SharedModule,
    AppRoutingModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



