import { Injectable } from '@angular/core';
import { Http, Headers, Response, RequestOptions, URLSearchParams } from '@angular/http';
import 'rxjs/Rx';
import { Observable } from 'rxjs/Observable';

import { NxService } from '../../shared/nx-library/nx.service';


@Injectable()
export class DataService {

  //BaseUrl = 'http://localhost:8081/';
  BaseUrl = 'http://54.158.33.81/';

  headers = new Headers({ 'Content-Type': 'application/json' });
  constructor(private http: Http, private nx: NxService) { }

  get(req: any) {

    let reqOption = new RequestOptions({
      params: req.params
    });

    let url = this.BaseUrl + req.url;

    return this.http.get(url, reqOption).map((res: Response) => {
      return res.json();
    });

  }

  post(req: any) {

    let url = this.BaseUrl + req.url;

    return this.http.post(url, req.params, { headers: this.headers }).map((res: Response) => {
      return res.json();
    });

  }


  private extractData(res: Response) {
    let body = res.json().data;
    return body || {};
  }
  private handleError(error: Response | any) {
    // In a real world app, we might use a remote logging infrastructure
    let errMsg: string;
    if (error instanceof Response) {
      const body = error.json() || '';
      const err = body.error || JSON.stringify(body);
      errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    console.error(errMsg);
    return Observable.throw(errMsg);
  }

}



