import { Injectable } from '@angular/core';

@Injectable()
export class NxMessageService {

  constructor() { }

  success(msg: any) { console.log(msg) };
  error(msg: any) { console.log(msg) };
  info(msg: any) { console.log(msg) };
  warning(msg: any) { console.log(msg) };

}



