import { Component, OnInit, ElementRef } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, FormArray, Validators } from '@angular/forms';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { DataService } from "../../../shared/services/data.service";
import { NxService } from "../../../shared/nx-library/nx.service";
import { AppConfig } from "../../../app.config";
declare var $: any;

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  registerForm: FormGroup;

  constructor(private fb: FormBuilder, private router: Router, private ds: DataService, private nx: NxService, private element: ElementRef) { 
    this.initializeForm();
  }



  ngOnInit() {
    setTimeout(function () { $('.card').removeClass('card-hidden'); }, 1000);
  }

  onSubmitForm() {
    let req = {
      url: AppConfig.Api.Auth.Register,
      params: this.registerForm.value
    };

    this.ds.post(req).subscribe((res) => {
      if (res.status === true) {
        this.nx.message.success(res.message);
      } else {
        this.nx.message.error(res.message);
      }
    })
  };

  routeChange(path: string) {
    this.router.navigate([path]);
  };

  private initializeForm() {
    this.registerForm = this.fb.group({
      firstName: ['', Validators.required],
      lastName: ['', Validators.required],
      email: ['', Validators.required],
      password: ['', Validators.required],
      acceptTemrs: [false, Validators.required],
    })

  }

  private validateForm(formGroup: FormGroup) {
    Object.keys(formGroup.controls).forEach(filed => {
      const control = formGroup.get(filed);
      if (control instanceof FormControl) {
        control.markAsTouched({ onlySelf: true });
      }
    })
  }

}



